import React, { Component } from "react";
import {Card,CardImg,CardText,CardBody,CardTitle,Breadcrumb,BreadcrumbItem,Button,Modal,ModalHeader,ModalBody,Row,Label} from "reactstrap";
import { Link } from "react-router-dom";
import { Control, LocalForm } from "react-redux-form";

//Note: export default reference s khng nhn ra import {CommentForm} from "./CommentForm" m nhn ra import CommentForm from "./CommentForm";
class CommentForm extends Component{
    //UI ca nt m form comment cng ging UI ca nt Login thuc HeaderComponent
    constructor(props) {
        super(props);
        //to add a new Modal to the application to host the form:
        this.state = {
           isModalOpen:false //ban u l ng <Modal></Modal>, Modal c thuc tnh quan trng isOpen nhn true hay false, thuc tnh isOpen l Controlled Component tr ti this.state.isModalOpen , which is setState() bi nhn nt Login
        };
        this.toggleModal=this.toggleModal.bind(this);
        this.handleComment = this.handleComment.bind(this);
    }

    //function called after form submission event
    handleComment(values) {//khng cn dng event.target.value
        this.toggleModal();
    
        this.props.postComment( //nhn cc bin u vo nh  nh ngha  ActionCreator.js. Cu hi l c th gi trc tip addComment m khng cn props khng
             this.props.dishId,
             values.rating,
             values.author, //values.username hay values.author ? Ghi sai thnh username l nhp xong s khng hin th tn tc gi
             values.comment);
        //event.preventDefault() , bi ging khng dng event, v cc sau khi submit form s khng c method GET truyn tham s ln thanh a ch
    }

    
    //toggle l tt - m - tt - .....    Sau khi hm toggleNav b called/invoked th state b cp nht, Nu Nav ang open th close, cn nu ang close th open
    toggleModal() {
        //Khi ngi dng 
        this.setState({
          isModalOpen: !this.state.isModalOpen
        });
    }
    render(){
        //https://www.w3schools.com/tags/att_input_type_range.asp

        //trong Modal, s dng Uncontrolled form, trong khi bi Tran Tien Dat dung React-redux-form
        return(
             
            <div>
                <b style={{marginLeft:"3vw"}}>Add your comment! </b>
                <Button outline onClick={this.toggleModal}>
                    <span className="fa fa-sign-in fa-lg">Comment</span>
                </Button>  

                 <Modal isOpen={this.state.isModalOpen} toggle={this.toggleModal}>
                         <ModalHeader>Submit Comment</ModalHeader>
                 
                         <ModalBody>
                         <LocalForm onSubmit={this.handleComment}>
                               <Row className="control-group">
                                 <Label htmlFor="rating" md={10}>
                                   Rating
                                 </Label>
                                 <Control.select
                                   model=".rating"
                                   className="form-control m-2"
                                   id="rating"
                                   name="rating"
                                   defaultValue="1"
                                 >
                                   <option>1</option>
                                   <option>2</option>
                                   <option>3</option>
                                   <option>4</option>
                                   <option>5</option>
                                 </Control.select>
                               </Row>

                               <Row>
                                 <Label htmlFor="author" md={10}>
                                   Your Name
                                 </Label>
                                 <Control.text
                                   model=".author"
                                   className="form-control m-2"
                                   id="author"
                                   name="author"
                                   placeholder="Your Name"
                                 ></Control.text>
                               </Row>
                               <Row className="control-group">
                                 <Label htmlFor="comment" md={10}>
                                   Comments
                                 </Label>
                                 <Control.textarea
                                   model=".comment"
                                   className="form-control m-2"
                                   id="comment"
                                   name="comment"
                                   rows="10"
                                 ></Control.textarea>
                                 <Button color="primary">Submit</Button>
                               </Row>
                             </LocalForm>
                           
                 
                         </ModalBody>
                     </Modal>
            </div>
            
        )
    }
}

//s l 0 references nu bn DishDetails khng import CommentForm
export default CommentForm;


