//component ny s khng lu state v form v s khng valdation cho form t ContactComponent.js

import React, { Component } from 'react';
import {FormGroup,Input,FormFeedback, Breadcrumb, BreadcrumbItem, Button, Label, Row, Col } from 'reactstrap';
import { Link } from 'react-router-dom';

//error: Module built failed nu c bin Form khc c extract destructure t reactstrap, Duplicate declaration "Form"
import { Control, Form} from 'react-redux-form'; //trong Lab09_2 th khng dng LocalForm na nn vi Form phi label cho n model="feedback". ;LU  l do form reset sau khi chuyn quan li cc menu ri v li Contact: L do dng Input m khng dng Control.text, khi dng Input s khng gi gi tr form sau khi i thm v, m cn khng lu c vo bin c alert
//Ngoi ra, sau khi submit form, th ni dung nhp form c reset li v mc nh

//Controlled form Validation
//https://www.logisticinfotech.com/blog/learn-redux-form-with-react-js/

//idea: NHng g nhp vo s c i tng Contact lu li trong instance property trng thi state, khi submit s alert
export default class ContactNoValidation extends Component { //Create a react component by extending the Component class

    constructor(props){
        super(props);

        //Khng cn state ring  lu form field Control.text na, v  c Redux store lu ri, NHNG cn state  validate form, nu b l bo li
        this.handleSubmit=this.handleSubmit.bind(this); 
    }

    
    handleSubmit(values) { //feedback chnh l i tng cha tt c thng tin ngi dng nhp aka form, v i tng ny nm trong i tng state, gi l model feedback
        console.log('Current state is: ' + JSON.stringify(values)); //trong Lab06_3_react-redux-form th LocalForm s lu thng tin ngi dng vo i tng values , gi l form state ch khng phi Contact component 's state, nn c th alert c
        alert('Current state is : '+JSON.stringify(values));
        this.props.resetFeedbackForm();
        //event.preventDefault();
    }

    render() {
        return(
            <div className="row row-content">
                <div className="col-12">
                    <h3>Send us your Feedback</h3>
                </div>
                <div className="col-12 col-md-9">
                
                    <Form model="feedback" onSubmit={(values) => this.handleSubmit(values)}>
                            <FormGroup row>
                                <Label htmlFor="firstname" md={2}>First Name</Label>
                                <Col md={10}>
                                    <Control.text type="text" id="firstname" name="firstname"
                                        placeholder="First Name"
                                        model=".firstname"
                                    />
                                  
                                </Col>
                            </FormGroup>

                            

                            <FormGroup row>
                                <Col md={{size: 10, offset: 2}}>
                                    <Button type="submit" color="primary">
                                        Send Feedback
                                    </Button>
                                </Col>
                            </FormGroup>
                    </Form>
                </div>
            </div>
        )
    }
}



