import React, { Component } from "react";
import {Card,CardImg,CardText,CardBody,CardTitle,Breadcrumb,BreadcrumbItem,Button,Modal,ModalHeader,ModalBody,Row,Label} from "reactstrap";
import { Link } from "react-router-dom";
import { Control, LocalForm } from "react-redux-form";
//import { addComment } from "../redux/ActionCreator";
import CommentForm from "./CommentForm";
import { Loading } from './LoadingComponent';
import { baseUrl } from '../shared/baseUrl';
import { FadeTransform, Fade, Stagger } from 'react-animation-components';


//FUNCTIONAL COMPONENT
//nu  export default DishDetails l khng ghi c const
//When destructuring objects, we use curly braces with the EXACT name of what we have in the object. Unlike in arrays where we can use any variable name to unpack the element, objects allow just the use of the name of the stored data.
const DishDetails = (props) => { //nhn d liu props t Main l 1 i tng dish, c dng map()  to Chuoi_JSX cho tng dish ??
    if(props.isLoading){ //true
        return(
            <div className="container">
                <div className="row">
                    <Loading/>
                </div>
            </div>
        )
    }
    else if(props.errMess){ //and isLoading is false
        return(
            <div className="container">
                <div className="row">
                    <h4>{props.errMess}</h4>
                </div>
            </div>
        )
    }
    //cu trc tng t MenuComponent.js
    else if (props.dish != null){
        return(
            //<Link to="/menu">Menu</Link> c class cha Main c c v Main nh 1 b hn hp Router-Switch  iu hng da trn Link, component nh cng ra , v return ca component l Chuoi_JSX
            //VD  Link to="/menu" ngha l c Main iu hng ti component Menu v tr Chuoi_JSX tng ng
            //Mi lin quan gia Breadcrumb v Link: "Breadcrumb" lets us navigate to different pages by providing the "Link" tags for the navigation hierarchy.
            <div className="container">
                <div className="row">
                <Breadcrumb>
                    <BreadcrumbItem>
                        <Link to="/">Home</Link>
                    </BreadcrumbItem>

                    <BreadcrumbItem>
                        <Link to="/menu">Menu</Link>
                    </BreadcrumbItem>
    
                    <BreadcrumbItem active>{props.dish.name}</BreadcrumbItem>
                </Breadcrumb>
                </div>
    
                <div className="row">
                    <div className="col-12 col-md-5 m-1">
                        <RenderDish dish={props.dish}/>
                    </div>
        
                    <div className="col-12 col-md-5 m-1">
                        <RenderCommentItem 
                            comments={props.comments}
                            postComment={props.postComment}
                            dishId={props.dish.id}
                            //author={props.comments.author} //c th khng cn, ch cn comments map ra cc comment sau  dng comment.author, th cch author ny v thay th comment.author
                        />
                        
                    </div>   
                </div>
            </div>
          
        )
    }else {
        return <div></div>;
    }
    
}


export function RenderDish({dish}) { //l cch khc thay v vit props, cch dng destructuring i tng javascript props, gn gi tr ca bin props.dish vo 1 bin mi vi exact name l "dish"
    //cc children c tr v bi callback ca map() phi c key
    return(
        <div key={dish.id} className="col-12 col-md-5 m-1">
            <FadeTransform
                in
                transformProps={{
                    exitTransform: 'scale(0.5) translateY(-50%)'
                }}>
                <Card>
                    <CardImg width="100%" src={baseUrl + dish.image} alt={dish.name}/>
                    <CardBody>
                        <CardTitle>{dish.name}</CardTitle>
                        <CardText>{dish.description}</CardText>
                    </CardBody>
                </Card>
            </FadeTransform>
        </div>
        
    );
}


export function RenderDish2(props) {
    //cc children c tr v bi callback ca map() phi c key
    const dish=props.dish;
    return(
        <div key={dish.id} className="col-12 col-md-5 m-1">
            <Card  body>
                <CardImg src={dish.image} alt={dish.name}/>
                <CardTitle>{dish.name}</CardTitle>
                <CardText>{dish.description}</CardText>
            </Card>
        </div>
        
    );
}


export function RenderCommentItem({comments,postComment,dishId}) {//ch  param th 2 l 1 function object khi c gi pha CommentForm s cn 4 tham s (dishId,rating,author,comment)
    //Ask mentor: khi destructure t props th c bt buc gn vo cc bin cng tn (addComments) vi cc thuc tnh ca props.addComment? Lab10_1 thy c
    if (comments != null){
        console.log(comments); //ok!!
        console.log(postComment);
        return(
        
            <div>
                <h4 style={{color:'blue'}}>Comments</h4>
                <ul className="list-unstyled">
                    <Stagger in>
                        {comments.map((comment)=>{
                           return(
                               <Fade in>
                                    <li key={comment.id}>
                                        <p>-------------Rated: {comment.rating} ----------------------</p>
                                        <b style={{color:"chocolate"}}>{comment.comment}</b>
                                        <p>--on {new Intl.DateTimeFormat('en-US', { year: 'numeric', month: 'short', day: '2-digit'}).format(new Date(Date.parse(comment.date)))} --</p>
                                        <b style={{color:"red"}}>-- By {comment.author} --</b>
                                    </li>
                               </Fade>
                              
                           )
                       })}
                    </Stagger>
                </ul>
                <CommentForm dishId={dishId} postComment={postComment}/>
            </div>
            //tuy nhin sau khi submit comment form, th d liu c truyn vi method GET ti trang ch vi URL nh: 
               /// http://127.0.0.1:5500/?username=lyvietkhang&rating=1&comment=TEST&remember=on#/
            //Lm sao grasp ly d liu t URL's param ?  https://karoldabrowski.com/blog/getting-parameters-from-url-in-a-react-application/
               /// iu kin dng useParams() l https://stackoverflow.com/questions/66506891/useparams-hook-returns-undefined-in-react-functional-component
    
        )
    }else{
        return <div></div>;
    }
    
}

//hm DishWithId trong Main cha c export th  DishDetails c 0 references
export default DishDetails;


