//reducer function dealing with dishes state
//reducer takes previous state and action as 2 parameters. TRONG , action l OPTIONAL
//https://www.robinwieruch.de/javascript-reducer/
//https://redux.js.org/tutorials/fundamentals/part-3-state-actions-reducers

//mun c reference phi c export
import { DISHES } from '../shared/dishes'; //khng cn
import * as ActionTypes from './ActionType';
//Dishes reducer khng cn lu state=DISHES na, m state s ty hnh ng
export const Dishes=(state={ isLoading: true,
        errMess: null,
        dishes:[]  
    }, //state ban u, s b thay i bng setState nh https://www.geeksforgeeks.org/how-to-fetch-data-from-an-api-in-reactjs/ (tuy nhin iu ny tri vi chn l ca Redux)  VD https://www.geeksforgeeks.org/how-to-fetch-data-from-an-api-in-reactjs/
    //nhn i tng hnh ng c dispatch ti server
    action)=>{
        console.log(action);
        switch (action.type) {
            //gi nguyn state trc v b sung cho n
            ////Meaning: whatever state is, I take the same state (a copy of state), then add. I am taking the CURRENT value of the state and something else after , will be applied as modifications to the ...state  
            case ActionTypes.ADD_DISHES: //?????? L load thnh cng dished v c dispatched ti store
                return {...state, isLoading: false, errMess: null, dishes: action.payload};
    
            case ActionTypes.DISHES_LOADING:
                return {...state, isLoading: true, errMess: null, dishes: []}
    
            case ActionTypes.DISHES_FAILED:
                return {...state, isLoading: false, errMess: action.payload}; //khi giao tip client-server c error, th ni dung error.message trong payload s hin trn trang web, VD 404 Not Found
    
            default:
                return state; //ban u
        }
}

