//(state) => stateProps
   /// mapStateToProps runs when:store state changes
   /// component re-renders when: any field of stateProps is different
   import React, { Component } from 'react';

   //define cc Child component references
   import MenuClassComponent from './MenuClassComponent';
   import HomePage from './HomepageComponent';
   import DishDetails from './DishdetailsComponent';
   import Contact from './ContactComponent';
   import ContactTranTienDat from './ContactTranTienDat';
   import ContactNoValidation from './Contact_without_validation';

   import AboutUs from './AboutusComponent'
   import Header from './HeaderComponent';
   import Footer from './FooterComponent';
  
   import { Switch, Route, Redirect, withRouter } from 'react-router-dom';
   import { connect } from 'react-redux';

   //Lab10_2 th addComment chnh l dispatcher c gi khi submit Comment vi LocalForm, khi  dispatcher ny s gi i tng B cha tt c gi tr ca model ti Redux store, ni y B c concat vo state mng hin ti ang cha tt c comments trong json-server c updated t comments=[] lc componentDidMount()
   //Trong Lab10_3 th addComment is no longer be DIRECTLY accessible by MainComponent, instead, postComment ,which contains addComment, adds a comment to database
   import {postComment, addComment,fetchDishes, fetchComments, fetchPromos } from '../redux/ActionCreator'; //nu dn sai l Module not found: Error: Can't resolve '../redux/ActionCreators' do sai tn th mc

   import { actions } from 'react-redux-form';
   import {TransitionGroup,CSSTransition} from 'react-transition-group';


//cho cc functional component ra ngoi render  th xem c animation khng
