//hm ny khng nm trong class Main, nu nm trong th s bind(this)
const mapStateToProps = state => { //tham s state l ly t Redux store, xem combineReducers tr v i tng state cha 4 anh no: dishes, comments, promotions, leaders
    // nhim v ca hm ny l map Redux store's state vo props c available nhn bi Main
    return {
        //cc thuc tnh sau state phi c trong initialState
        //state c returned bi Reducer
        dishes: state.dishes,
        comments: state.comments,
        promotions: state.promotions,
        leaders: state.leaders
        //tuy nhin mun ly cc gi tr trn t Redux Store th phi connect trc , ch cn wrap Main component inside the connect()
    }
    //Thng thng l return() , nhng  y l return{ } tr v l props object,  truy cp bi this.props.dishes
}

//receive dispatch function from store as parameter , v trong Javascript onClick nhn 1 i tng hm vi tn gi 

const mapDispatchToProps=(dispatch)=>({ //mapDispatchToProps l hm nhn hm dispatch ca store v tr v cc method definitions


     //by gi, UI component Main c th invokes Action  to object
     //dispatch<A extends Action>(action)

     //how to obtain action as parameter of despatch function? v hm l object nn hm c th lm tham s v object c th lm tham s, th ch cn cho TN hm (nu  TNhm() th s thc hin hm ngay nu khng bt buc chy sau nh callback) vo v tr tham s nu mun hm l callback thc hin sau
     
    // Trng hp ny th gi hm addComment() mun hm addComment() thc hin to ActionObject ngay lp tc khng ch i
    // vic gi hm dispatch() ca store s gip ActionObject to store

    //https://react-redux.js.org/using-react-redux/connect-mapdispatch
        ///The mapDispatchToProps function will be called with dispatch as the first argument. You will normally make use of this by returning new functions that call dispatch() inside themselves, and either pass in a plain action object directly or pass in the result of an action creator.

    //phi gi l dispatching_addComment
    postComment:(dishId,rating,author,comment)=>dispatch(postComment(dishId,rating,author,comment)), //ghi di vy ch thc ra l hm dispatch(action) phn phi action ti store
    //this can be used/available within Main component here after connect() . Then what should we make use of it in connected Main Component?
    //Tham s ca hm dispatch l g? either pass in a plain action object directly or pass in the result of an action creator.

    //map fetchDishes vo Props vi tn bin mi l fetchDishes
    fetchDishes: () => { dispatch(fetchDishes())},  //v phi fetchDishes() l 1 thunk, dispatch gip cho fetchDishes() available cho application
    //now where I will fetch the dishes? componentdidmount gip fetch sau khi i tng Main i vo DOM , trong life cycle ca Main

    //thm 1 dispatch cho vic reset form Contact, truy cp dispatch ny bng this.props
    resetFeedbackForm: () => { dispatch(actions.reset('feedback'))}, //actions.reset l built-in , vy "feetback l g". The form will be named/labelled as "feedback", hover thy cng thc tham s l model, v form l 1 i tng trong state nn gi form l model. 
    //Nh vy Main c th truyn dispatch ny cho Contact thng qua props
    //Ti Contact, gn nhn cho form l <Form model="feedback" v onSubmit s kch hot hm dispatch lm reset form : this.props.resetFeedbackForm();
    //ti sao li phi cho dispatch(actions.reset('feedback')) nm trong 1 arrow function ri truyn ti Contact??  v nu  theo kiu gi ham() th hm s chy ngay trong Main, trong khi yu cu hm ch chy khi submit form trong Child component l Contact
    fetchComments: () => dispatch(fetchComments()), //xem ActionCreator fetchComments()
    fetchPromos: () => dispatch(fetchPromos())  //hm dispatch c nhn t store
})


class Main extends Component {
    constructor(props) { 
        super(props); 
    }

    componentDidMount() {
        //call/execute/invoke cc dispatchers , this is very good time to fetch any data required for my application. Theo l thuyt th lc ny UI MainComponent ang invokes ActionCreator/Thunk
        this.props.fetchDishes(); //v trc  dispatch  a hm to hnh ng fetchDishes vo trong props fetchDishes, nn bin mi ny thnh hm v c call bng ()
        this.props.fetchComments(); //sau khi Main vo DOM th mng json comments c sn cng c nhn t server, do  khi ngi dng navigate ti DishDetailsComponent s khng cn xt Comment c isLoading hay khng v  c ri
        this.props.fetchPromos();
    }

    render(){
       
        console.log(this.props.dishes); //ok. Khi  HomePage th object ny l {isLoading: true, errMess: null, dishes: Array(0)} . Xem tip cng cu lnh ny ti MenuClassComponent s nhn mng 4 dishes
        console.log(this.props.promotions); //ok
        console.log(this.props.leaders); //ok
        console.log(this.props.comments);//ok
        console.log(this.props.postComment);
        /*
        const DishWithId = ({match}) => { 
            //thuc tnh th ba l addComment s gip component DishDetails gin tip gi c thng tin user has submitted ti Store thng qua Action Creator v Action
            return(
                <DishDetails 
                    dish={this.props.dishes.dishes.filter((dish) => dish.id === parseInt(match.params.dishId,10))[0]} 
                    isLoading={this.props.dishes.isLoading}
                    errMess={this.props.dishes.errMess}
                    comments={this.props.comments.comments.filter((comment) => comment.dishId === parseInt(match.params.dishId,10))}
                    commentsErrMess={this.props.comments.errMess}
                    addComment={this.props.addComment}
                
                />
            );
        };
        */

        return(
            //https://stackoverflow.com/questions/50290408/how-to-use-react-ga-with-hashrouter
                /// Warning: < HashRouter> ignores the history prop.
                /// https://www.hkinfosoft.com/blog/dynamic-transitions-with-react-router-and-react-transition-group/
                    //// thc ra this.props.location By default, a switch uses history.location to select the route to render. However you can provide a location prop to the switch that will override the default history.location value
                /// https://tech.lalilo.com/dynamic-transitions-with-react-router-and-react-transition-group
            // thuc tnh ca component Switch
                /// <Switch location={{location}}l cch extract destructure bin location t props https://www.freecodecamp.org/news/the-basics-of-destructuring-props-in-react-a196696f5477/
                /// tng ng vi <Switch location={this.props.location} trong ti liu Reading ca Coursera
                /// tuy nhin Muppala khng vit location cho Switch cng ok

                //
                <div>
                    
                    <Header/>

                    <TransitionGroup>
                        <CSSTransition key={this.props.location.key} classNames="page" timeout={300}>
                            <Switch location={this.props.location}>
                                <Route exact path='/' component={()=>(
                                    <HomePage
                                        DISHES={this.props.dishes.dishes.filter((dish) => dish.featured)[0]}
                                        DISHLOADING={this.props.dishes.isLoading}
                                        DISHERRORMESSAGE={this.props.dishes.errMess}

                                        PROMOTION={this.props.promotions.promotions.filter((promo) => promo.featured)[0]}
                                        PROMOLOADING={this.props.promotions.isLoading}
                                        PROMOERRORMESSAGE={this.props.promotions.errMess}

                                        LEADERS={this.props.leaders.filter((leader) => leader.featured)[0]}
                                    />
                                )} /> 
                                <Route exact path='/aboutus' component={() => <AboutUs leaders={this.props.leaders} />} />
                                <Route exact path='/menu' component={() => <MenuClassComponent dishes={this.props.dishes.dishes} message={<b style={{color:'orange'}}>thm param 0,1,2,3 trn URL, hoc click hnh mn n  xem chi tit mn n</b>}/>} />
                                <Route path='/menu/:dishId' component={({match})=><DishDetails   //khng phi gi DishDetails bnh thng m phi cho n vo 1 hm nhn tham s match
                                     dish={this.props.dishes.dishes.filter((dish) => dish.id === parseInt(match.params.dishId,10))[0]} 
                                     isLoading={this.props.dishes.isLoading}
                                     errMess={this.props.dishes.errMess}

                                     comments={this.props.comments.comments.filter((comment) => comment.dishId === parseInt(match.params.dishId,10))} //1 mn (1 dishId) s c nhiu comments khc nhau
                                     commentsErrMess={this.props.comments.errMess}
                                     postComment={this.props.postComment}
                                />} />
                                <Route exact path='/contactus' component={()=><ContactTranTienDat resetFeedbackForm={this.props.resetFeedbackForm}/> } />
                                <Redirect to="/" />    
                            </Switch>
                        </CSSTransition>
                    </TransitionGroup>    
                    
                    <Footer/>
                      
                </div>

                            
     
        )
    }
}





//nu  withRouter trong class Main th Error: Main is not defined
//https://stackoverflow.com/questions/67215660/how-access-redux-mapped-props-from-outside-a-connected-component

//ch : vi React component phi t tn l ConnectedMainComponent (ghi connectedMainComponent l sai), vi HTML element th lowercase

//connect MainComponent to mapStateToProps [  dishes,comments,promotions,leaders available cho MainComponent s dng] v mapDispatchToProps  [  addComment c available cho MainComponent s dng]
//https://stackoverflow.com/questions/69934351/withrouter-is-not-exported-from-react-router-dom
 //nh connect vi 

//Syntax error: C:\localhost\HK120212022_cacMonHoc\FUNIX\KHOA_3_WebFrontEnd_ReactJS\_N_V EXERCISES_V_LABS\LABS\Lab08_2_Muppala_createReactApp\lab08_2\src\components\MainComponent.js: Only one default export allowed per module. (170:0)
export default withRouter(connect(mapStateToProps, mapDispatchToProps)(Main));
//thy n: if you use React Router then  withRouter is required to connect React app to Redux architecture and React Router
//https://stackoverflow.com/questions/53539314/what-is-withrouter-for-in-react-router-dom
//As the second argument passed in to connect, mapDispatchToProps is used for dispatching actions to the store.
