import * as ActionTypes from './ActionType';
import { DISHES } from '../shared/dishes'; //khng dng Dishes reducer na
import {baseUrl} from '../shared/baseUrl'; //now we have fetched or include the baseUrl
//nhn tt c Action Type t ActionType.js
//a function that creates an action object, and this function is invoked by Component UI
//arrow function nhn 4 parameters v return an action object { ... }
//Then, the action object is dispatched to a Reducer inside a state Store
//in Store, which part of the global state will be affected? you will send various part of a comment to Store
export const addComment=(comment)=>(
  //BEFORE: onSubmit CommentForm, nhn 4 thng tin ngi dng ri c dispatch ti Redux bi Main component
  //AFTER: khng nhn comment gm 4 thng tin ca ngi dng na (do postComment  lm), thc t l nhn OK_response t server tr v, ri gi thnh 1 i tng  postComment dispatch n i ti Redux store
  {  //l hm to i tng da trn comment ca ngi dng, ri chuyn ti Redux store thng qua hm dispatch() ca store
    type:ActionTypes.ADD_COMMENT,
    //payload contains whatever to be carried in the Action object towards the Reducer to add comment
    //payload:comment //l bi ging Muppala, tng ng trong Reducer comments.js s truy cp bng var comment = action.payload  //nu ghi payload:{comment:comment}  th sau khi submit comment khng hin ngay lp tc, phi F5 mi ra
    payload:{comment:comment} //th bn tng ng trong Reducer comments.js s truy cp bng var comment = action.payload.comment;
  }
);


//TRONG LAB10_3 s post comment mi vo json-server  
//postComment l 1 function of function s phi hp vi addComment  gi i tng comment mi t UI component ti json-server  push vo mng comments trong db.json
//postComment va l dispatcher (khi submit form addComment vo Store) va l ni push i tng ln json-server
export const postComment=(dishId,rating,author,comment)=>(dispatch)=>{ //Thunk ny tr v 1 hm which nhn dispatch lm tham s
    //this will perform on the server 
    //and the function addComment now simpler because it just receice the comment than the old way we have done
    //What do we do when postComment nhn 4 tham s? To 1 Javascript object, sau  map 4 tham s nhn c vo trong Javascript object  
    
    const newComment={
      dishId:dishId, //Ch : khng phi comment id ca newComment v ci ny c server t ng to khi push mi vo mng
      rating:rating,
      author:author,
      comment:comment
    }
    //add a new property to above Javascript object
    newComment.date = new Date().toISOString();

    //push the comment to the server. DO fetch operation here, but in this case, use POST operation, so you need to supply value for it
       /// recall that POST operation requires you send data into body of message
       /// In addition to the body, set up headers application/json
    return fetch(baseUrl+'comments', 
       // now we define the structure of request message: body, headers, method,
        {
          method: "POST",//POST s va gi va nhn d liu //nhng bi trc khng ni g th default method to GET OPERATION
          body: JSON.stringify(newComment), //json ha javascript object, ri cho vo trong body trong POST message we send out
          headers: {
            "Content-Type": "application/json" //recall that the POST body contains data in JSON format
          },
          credentials: "same-origin" //we will deal with it in NodeJS course: same-origin, cross-origin
        }) //thiu ) ch ny th TypeError: {(intermediate value)(intermediate value)(intermediate value)(intermediate value)}.then is not a function
        //When the fetch operation is carried out, obviously you will receive a response from server. So how you handle response 
        // very similar way we did earlier for GET operation,nn copy paste vo y
        .then(response => {
             if (response.ok) {
               return response;
             } else {//khng nhn c tr li t server
               var error = new Error('Error ' + response.status + ': ' + response.statusText);
               error.response = response;
               throw error;
             } 
        },
        //nhn c tr li nhng l bo li
        error => {
            var errness=new Error(error.message);
            throw errness;
        })
       .then(response => response.json())

       //d liu updated response.json() nhn c t server  bao gm newComment v id c gn cho n
       .then(response => dispatch(addComment(response))) //s dng 
       .catch(error =>  { 
            console.log('post comments', error.message); 
            alert('Your comment could not be posted\nError: '+error.message); 
       })
    
    //That is how you will POST comment to server: Va SEND va RECEIVE
        /// create POST message and SEND to server 
        /// RECEIVE the response that should be the updated comment array that will be pushed to Redux store by doing dispatch
        /// if receive error res, just print to log and pop up alert
    //How to make use of postComment?
        /// update reducer comment.js ti ADD_COMMENT, nhng trc , the comment posted bu user cn chnh sa thng tin ti server side trc khi tr v ri lu vo Redux store
        ///Tip theo, chnh sa gip cho MainComponent nhn quyn c gi postComment nhm kch hot vic chuyn i tng c cha commnent mi vo Redux store
};




//Minh ha 1
    let url = "http://jsonplaceholder.typicode.com/posts/6";

    let iterator = fetch(url);
    
    iterator
      .then(response => {
          return {
              data: response.json(),  //cu hi l data l Promise object hay ch l standard object which you can access property
              status: response.status //thng res.status  pht hin li trong giao tip client-server
          }
      })
      //post l i tng kt qu t return bn trn, hover xem kt qu
      .then(post => console.log(post.data.title)); //post.data c prototype l Promise object v khng truy cp c gi tr, VD post.data.title
    ; //Why does response.json return a promise? 

//Minh ha 2
    let iterator2 = fetch(url);

    iterator2
      .then(response => response.json()) //CH : json() lun tr v Promise object , cho d c keyword "return" hay khng
      //Why do I get the value post.title if I return the promise from the then handler? Because that's how promises work. The ability to return promises from the callback and get them adopted is their most relevant feature, it makes them chainable without nesting.
      .then(post => console.log(post.title));//post l i tng ly t API , l standard object v truy cp c d liu v title: dolorem eum magni eos aperiam quia
    ;
    //https://stackoverflow.com/questions/37555031/why-does-json-return-a-promise
  
    
//fetch d liu dishes + addDishes + dishesLoading + dishesFailed

   /// nh ngha fetchDishes
   ///Tham kho: https://reactgo.com/redux-fetch-data-api/ +
  /// https://stackoverflow.com/questions/60556683/redux-thunk-fetch-api-action-and-reducer
  /// https://www.linkedin.com/pulse/redux-thunk-middleware-context-api-action-state-store-mostafa-mohamed
export const fetchDishes=()=>(dispatch)=>{  //hm to hnh ng hay Thunk ny trong Lab09_3 l gi hnh ng addDishes(dishes) ti store , cn trong Lab10_1 th u tin l "we issue the Fetch request to the server " , d liu DISHES nhn t server s c gi trong "action" object bng cch DISHES => dispatch(addDishes(DISHES))) v gi ti Dishes reducer trong redux/dishes.js, ni m ty ActionTypes m tr v state tng ng
    //va gi dispatch va gi hm to hnh ng vi mc ch loading chy cng lc
    dispatch(dishesLoading(true)); //lt s bit dishesLoading s lm g

    return fetch(baseUrl + 'dishes') //hover s thy fetch tr v Promise object l response
    //when we encounter errors, first thing is how to handling errors
    //note that server's response could be data response or error response 
    //Whatever the case, we need to handle problems 
    .then( //SITUATION #1: YOU ACTUALLY RECEIVE A RESPONSE FROM SERVER, AND THIS MAY BE NOT_OK RESPONSE
        response => {
        //what do you do if you receive the response correctly? 
        if (response.ok) {
          return response;  //we will just return the response. After that, the received response will be available for the next "then"
        } else { //Except 200 code, you encounter errors, what do you do
        //generate an error object in Javascript, inside it we set up a message to be delivered "Error detected"
        //extract a status containing response status code from response and a statusText
        // 300,400,500 are response error codes we will take as status, 200 is ok which we already handled in if
        //statusText contains any error message that server sends back
          var error = new Error('Error detected' + response.status + ': ' + response.statusText); //join together in error message
          error.response = response; //gn cho error
          //throw error in a promise handler
          throw error; //(1)
        }
      },
      //some situation the server does not even respond
      //so promise handling should be implemented
      //error handler 

      //SITUATION #2: YOU DON'T HERE BACK ANYTHING FROM THE SERVER (th Ctr+C to shut down json-server then React client trying to talk to server , it will fail)
      //so you must handle error appropriately
      error => {
            var errmess = new Error(error.message); //khi error pht sinh trong giao tip client-server, s c thng tin trong error.message
            throw errmess; //(2) //THNG thng l bo ngay trn trang web: Failed to fetch
      })
    //cc arrow function nm trong then() u l cc hm callback (to handle response parameter coming)

    //If you know how promises work, when the above returns something, that will be delivered in as an incoming parameter to the next "then" vi cc promises handler  y
    .then(response => response.json()) //hover s thy fetch tr v Promise object l dishes

    //D liu dishes ly t server s c add thm na
    .then(dishes => dispatch(addDishes(dishes))) //hover s thy json() tr v Promise object (l 1 object i vo hm Promise trong then?) l dishes
    //khi obtain c dishes object, th dng hm dispatch ca store  chuyn action object ti store 
    //implement catch here to catch the error then handle the error appropriately
    .catch(error => dispatch(dishesFailed(error.message))) //we dispatched a dishesFailed action with the error message included. This will ensure your state will be updated with error message
    //throw then catch error
    //In handling of promises, an error will cause a rejected promise . Therefore we will catch using the .catch of promises . Inside the catch you will receive the error
    //the catch would be caused because you throw an error at (1) or at (2). Either case we will handle error appropriately 

    
    
    
    
    //That's all, this is how we handle response we receive from server
}

export const addDishes = (dishes) => ({
    type: ActionTypes.ADD_DISHES,
    payload: dishes   //Lab10_2 sau ny khi fetch thnh cng d liu v l 1 mng dishes, s gi hm addDishes(mng)
});

export const dishesLoading = () => ({ // nu cho 3 hm ny vo trong fetchDishes th Syntax error: 'import' and 'export' may only appear at the top level (53:0)
    type: ActionTypes.DISHES_LOADING
});
    
export const dishesFailed = (errmess) => ({
    type: ActionTypes.DISHES_FAILED,
    payload: errmess
    //errmess l string nhng khng ni r  y
});
    
//fetch d liu comments + commentsFailed + addComments
export const fetchComments = () => (dispatch) => {    
    //comments s c load ngay khi HomePage loaded, nn khng dng commentsLoading
    return fetch(baseUrl + 'comments')
    .then(response => {
        if (response.ok) {
          return response;
        } else {
          var error = new Error('Error ' + response.status + ': ' + response.statusText);
          error.response = response;
          throw error;
        }
      },
      error => {
            var errmess = new Error(error.message);
            throw errmess;
      })
    .then(response => response.json())
    .then(comments => dispatch(addComments(comments)))  //phn bit hm to hnh ng addComment() bn trn
    .catch(error => dispatch(commentsFailed(error.message)));
};

export const commentsFailed = (errmess) => ({
    type: ActionTypes.COMMENTS_FAILED,
    payload: errmess
}); //gi error message vi thng tin lin quan thnh mt i tng  chuyn i

//c s dng bi componentDidMounted --> fetchComments() --> nhn response t server ri addComments
export const addComments = (comments) => ({ //khc hm to hnh ng  addComment   nh ngha  trn
    type: ActionTypes.ADD_COMMENTS, //ch : hm addComment pha trn cng dng action type ADD_COMMENT
    payload: comments
}); //tht ra l gi d liu mng bnh lun nhn t server cng cc thng tin lin quan, ri c dispatch ti Redux store



//fetch d liu Promotions + promosLoading + promosFailed + addPromos
export const fetchPromos = () => (dispatch) => {
    
    dispatch(promosLoading(true));

    return fetch(baseUrl + 'promotions')
    .then(response => {
        if (response.ok) {
          return response;
        } else {
          var error = new Error('Error ' + response.status + ': ' + response.statusText);
          error.response = response;
          throw error;
        }
      },
      error => {
            var errmess = new Error(error.message);
            throw errmess;
      })
    .then(response => response.json())
    .then(promos => dispatch(addPromos(promos)))
    .catch(error => dispatch(promosFailed(error.message)));
}

export const promosLoading = () => ({
    type: ActionTypes.PROMOS_LOADING
});

export const promosFailed = (errmess) => ({
    type: ActionTypes.PROMOS_FAILED,
    payload: errmess
});

export const addPromos = (promos) => ({
    type: ActionTypes.ADD_PROMOS,  
    payload: promos
});

