import React, { Component } from 'react';
import {FormGroup,FormFeedback,Input, Breadcrumb, BreadcrumbItem, Button, Label, Row, Col } from 'reactstrap';
import { Link } from 'react-router-dom';

//error: Module built failed nu c bin Form khc c extract destructure t reactstrap, Duplicate declaration "Form"
import { Control, Form, Errors,actions} from 'react-redux-form'; //trong Lab09_2 th khng dng LocalForm na nn vi Form phi label cho n model="feedback". Cn actions  dng trong Main, vy  y lm g?


//Controlled form Validation
//https://www.logisticinfotech.com/blog/learn-redux-form-with-react-js/


//LU : do Control ca react-redux-form khng c dng thay v Input, nn form khng c tc dng lu d liu khi qua li cc trang SPA. Ngoi ra cng khng submit d liu c
export default class Contact extends Component { //Create a react component by extending the Component class

    constructor(props){
        super(props);

        
        //Component takes fomr's current value through props
        this.state={
            //key l cc thuc tnh tn (name) ca th input
            
            firstname:'',
            lastname:'',
            telnum:'',
            email:'',
            agree:false,
            contactType:'Tel.',
            message:'',
            
            touched:{
                firstname:false,
                lastname:false,
                telnum:false,
                email:false,
            }
        }
        //Khng cn state ring  lu form field input na, v  c Redux store lu ri, NHNG cn state  validate form, nu b l bo li
        this.handleInputChange=this.handleInputChange.bind(this);
        this.handleSubmit=this.handleSubmit.bind(this); //this contains a reference to React DOM Contact
        //https://codegrepr.com/question/jquery-checkbox-event-handling/
        this.handleBlur=this.handleBlur.bind(this); //v phi handleBlur l functional component nn khng cn bind vo class
        this.validate=this.validate.bind(this);
    }

    //Unlike the uncontrolled component, the input form element in the controlled component is handled by the component rather than the DOM. It takes its current value through props. The changes are notified through callbacks.
    //https://www.w3schools.blog/forms-reactjs
    
    //https://newbedev.com/javascript-react-checkbox-event-target-value-code-example
    
    handleInputChange(event) {
        //input type=text th nhn value, cn input type checkbox th nhn checked
        //[event.target.name] l gi tr ca tn ca input DOM  ni cch khc l <input name="gi tr"
        const target_DOM_element=event.target;//event is already set to / contains reference to the input's DOM element
        //DOM input th c type hoc checkbox hoc text
        const value_of_input_DOM= target_DOM_element.type === 'checkbox' ? target_DOM_element.checked : target_DOM_element.value; // <input checked={}
        const name = target_DOM_element.name;
        //https://stackoverflow.com/questions/38899884/how-to-use-square-brackets-in-react-setstate
        /// thay v ghi target.name : value trong setState th dynamically   [name]:value v input's name c th l mt trong cc state instance property trong this.state 
        this.setState({
          [name]: value_of_input_DOM
        });
    }

    handleSubmit(values) { //feedback chnh l i tng cha tt c thng tin ngi dng nhp aka form, v i tng ny nm trong i tng state, gi l model feedback
        console.log('Current state is: ' + JSON.stringify(values)); //trong Lab06_3_react-redux-form th LocalForm s lu thng tin ngi dng vo i tng values , gi l form state ch khng phi Contact component 's state, nn c th alert c
        alert('Current state is : '+JSON.stringify(values));
        this.props.resetFeedbackForm();
        //event.preventDefault();
    }

    //khi input field no  b loosed focus (ngha l mang con tr ra ngoi)
    // ... l Spread Operator  combine cc key:value khc [field_loosed_focus] vo trong this.state.touched 
    handleBlur=(field)=>(event)=>{
        this.setState(
            {
                touched:{...this.state.touched,[field]:true}
            }
        )
    }

    validate(firstname,lastname,telnum,email){
        const errors={
            //ch  nu b cc if else pha sau y th cc params b ti mu
            //chng t cc thuc tnh ca i tng errors khng lin quan g cc tham s hm
            firstname:'',
            lastname:'',
            telnum:'',
            email:''
        } //nu gia nguyn c cc gi tr '' th hon ton valid (v 4 input fields trn khng c li no)

        //if(this.state.touched.firstname)  ngha l if(this.state.touched.firstname==true) 

        if (this.state.touched.firstname && firstname.length < 3)
            errors.firstname = 'First Name should be >= 3 characters';
        else if (this.state.touched.firstname && firstname.length > 10)
            errors.firstname = 'First Name should be <= 10 characters';

        if (this.state.touched.lastname && lastname.length < 3)
            errors.lastname = 'Last Name should be >= 3 characters';
        else if (this.state.touched.lastname && lastname.length > 10)
            errors.lastname = 'Last Name should be <= 10 characters';

        //const reg = /^[0-9\b]+$/;
        //https://www.itsolutionstuff.com/post/react-phone-number-validation-exampleexample.html
        //https://www.javatpoint.com/javascript-form-validation
        var pattern = new RegExp(/^[0-9\b]+$/);
        if (this.state.touched.telnum && !pattern.test(telnum))
            errors.telnum = 'Tel. Number should contain only numbers';

        if(this.state.touched.email && email.split('').filter(x => x === '@').length !== 1)
            //ct chui email thnh tng k t cch nhau ''
            //https://www.w3schools.com/jsref/jsref_split.asp
            errors.email = 'Email should contain a @';

        return errors;
    }

    


    render() {
        const errors=this.validate(this.state.firstname, this.state.lastname, this.state.telnum, this.state.email);
        return(
            
            //nu <Form><Form/> th bo li JSX element Form has no corresponding closing tag, phi l <Form></Form>
            // HTML class form-group
            //https://stackoverflow.com/questions/62426823/reactstrap-input-validation-error-help-needed
            //https://thewebdev.info/2020/08/01/reactstrap%E2%80%8A-%E2%80%8Aform-validation-and-customizations/

            //errors l tng hp tt c li nhp liu trn form
            //Reactstrap.Input s c thuc tnh valid=true hoc invalid=true, tng t <Reactstrap.FormFeedback> cng cn

            //CH : khi Input c thuc tnh invalid=false th c FormFeedback
            <div className="row row-content">
                <div className="col-12">
                    <h3>Send us your Feedback</h3>
                </div>
                <div className="col-12 col-md-9">
                
                    <Form model="feedback" onSubmit={(values) => this.handleSubmit(values)}>
                            <FormGroup row>
                                <Label htmlFor="firstname" md={2}>First Name</Label>
                                <Col md={10}>
                                    <Input type="text" id="firstname" name="firstname"
                                        placeholder="First Name"
                                        value={this.state.firstname}
                                        valid={errors.firstname===''}
                                        invalid={errors.firstname!==''}
                                        onBlur={this.handleBlur('firstname')}
                                        onChange={this.handleInputChange} 
                                    />
                                    <FormFeedback>{errors.firstname}</FormFeedback>    
                                </Col>
                            </FormGroup>

                            <FormGroup row>
                                <Label htmlFor="lastname" md={2}>Last Name</Label>
                                <Col md={10}>
                                    <Input type="text" id="lastname" name="lastname"
                                        placeholder="Last Name"
                                        value={this.state.lastname}
                                        valid={errors.lastname === ''}
                                        invalid={errors.firstname!==''}
                                        onBlur={this.handleBlur('lastname')}
                                        onChange={this.handleInputChange} />
                                    <FormFeedback>{errors.lastname}</FormFeedback>
                                </Col>
                            </FormGroup>
        
                            <FormGroup row>
                                <Label htmlFor="telnum" md={2}>Contact Tel.</Label>
                                    <Col md={10}>
                                        <Input type="tel" id="telnum" name="telnum"
                                            placeholder="Tel. number"
                                            value={this.state.telnum}
                                            valid={errors.telnum === ''}
                                            invalid={errors.telnum!==''}
                                            onBlur={this.handleBlur('telnum')}
                                            onChange={this.handleInputChange} />
                                        <FormFeedback>{errors.telnum}</FormFeedback>
                                    </Col>
                            </FormGroup>

                            <FormGroup row>
                                <Label htmlFor="email" md={2}>Email</Label>
                                <Col md={10}>
                                    <Input type="email" id="email" name="email"
                                        placeholder="Email"
                                        value={this.state.email}
                                        valid={errors.email === ''}
                                        invalid={errors.email !== ''}
                                        onBlur={this.handleBlur('email')}
                                        onChange={this.handleInputChange} />
                                    <FormFeedback>{errors.email}</FormFeedback>
                                </Col>
                            </FormGroup>

                            <FormGroup row>

                                <Col md={{size: 6, offset: 2}}>
                                    <FormGroup check>
                                        <Label check>
                                            <Input type="checkbox"
                                                name="agree"
                                                checked={this.state.agree}
                                                onChange={this.handleInputChange} /> {' '}
                                            <strong>May we contact you?</strong>
                                        </Label>
                                    </FormGroup>
                                </Col>

                                <Col md={{size: 3, offset: 1}}>
                                    <Input type="select" name="contactType"
                                            value={this.state.contactType}
                                            onChange={this.handleInputChange}>
                                        <option>Tel.</option>
                                        <option>Email</option>
                                    </Input>
                                </Col>

                            </FormGroup>

                            <FormGroup row>
                                <Label htmlFor="message" md={2}>Your Feedback</Label>
                                <Col md={10}>
                                    <Input type="textarea" id="message" name="message"
                                        rows="12"
                                        value={this.state.message}
                                        onChange={this.handleInputChange}></Input>
                                </Col>
                            </FormGroup>

                            <FormGroup row>
                                <Col md={{size: 10, offset: 2}}>
                                    <Button type="submit" color="primary">
                                        Send Feedback
                                    </Button>
                                </Col>
                            </FormGroup>
                    </Form>
                </div>
            </div>
        )
    }
}



