import React from 'react'; 
import {Link} from 'react-router-dom';
import {Card,CardImg,CardImgOverlay,CardBody,CardTitle,CardSubtitle,CardText,Breadcrumb, BreadcrumbItem } from 'reactstrap';
import { Loading } from "./LoadingComponent";
import {baseUrl} from '../shared/baseUrl';

class MenuClassComponent extends React.Component {
    constructor(props) { //props l mng dishes nhn t Main  Route, gi c mt i tng this.props.dishes   :  component={() => <Menu dishes={this.props.dishes} />
        super(props);
        this.Trinh_bay_cac_mon_trong_menu=this.Trinh_bay_cac_mon_trong_menu.bind(this);  //v tri mu vng do v phi l arrow function
    }

    //tham s hm ca map() , l arrow function nn cng khng cn bind to class component 
    Trinh_bay_cac_mon_trong_menu=(dish)=>{ //map hiu dish l 1 phn t ca dishes
        console.log(dish.id) //OK, lit k ton b 4 dish.id
        return(
            //column .col-12 col-md-5 m-1 ca row
            //ch  <Reactstrap.Card body> l ok, cn <Reactstrap.CardBody> </Reactstrap.CardBody> l undefined type khi i vo hm React.createElement(type,classNamr,children)
            <div key={dish.id} className="col-12 col-md-5 m-1">
                <Card key={dish.id} 
                    //onClick={() => this.props.onClick(dish.id)}
                >
                    <Link to={`/menu/${dish.id}`}>
                         <CardImg width="100%" src={baseUrl + dish.image} alt={dish.name}/>
                         <CardImgOverlay>
                             <CardTitle>{dish.name}</CardTitle>
                         </CardImgOverlay>
                    </Link>
                    
                </Card>
            </div>
        )
    }

    //render() l hm bt buc c trong Component LifeCycle, v render() s nhn tt c cc hm m return(Chuoi_JSX). Hm th bt buc cha return()
    render() {
        //Nhn props.dishes t Main v p dng map()
        //nhn props.onClick t Main v o dng cho callback ca hm map()
        console.log(this.props.dishes); //Xem lc  HomePage cng cu lnh ny s ra {isLoading: true, errMess: null, dishes: Array(0)}. Cn ti MenuClassComponent th nhn mng 4 mn n => OK
        const menu=this.props.dishes.map(this.Trinh_bay_cac_mon_trong_menu);
        const message=this.props.message;
        console.log(menu);
        //https://www.geeksforgeeks.org/how-to-pass-data-from-child-component-to-its-parent-in-reactjs/

        //do {message}  c b tag, th return ny b s tng  m
        return(
            <div className="container">
                <b>{message}</b>
                <div className="row">             
                    {menu}
                </div>
            </div>
            
        ) 
    }
}

export default MenuClassComponent;


