//c chu nh hng t Action Type 
//import { COMMENTS } from "../shared/comments";   <== Khng cn thit na, v d liu s ti t the Air
import * as ActionTypes from "./ActionType";
//hm sau s make change initial state v  c mt gi i tng mang payload c dispatch ti. Reducer s extract thng tin t i tng v s to phin bn mi ca state
export const Comments=(state= { 
        errMess: null, 
        comments:[]
      },action)=>{ //state in parameter cannot be modified, ch c cch dng 1 bn sao ca n ri modify v return
    switch (action.type) {
        case ActionTypes.ADD_COMMENTS: //WHEN? khi componentDidMount() th cc comments c sn trong b d liu c
          return {...state, errMess: null, comments: action.payload};

        case ActionTypes.ADD_COMMENT: //WHEN? lin quan bin c ngi dng nhp bnh lun
            var comment = action.payload.comment;
            /* comment's Id v date s c loi b trong Lab10_3 th comment mi  c thm vo mng trn server v server automatically add id cho comment mi  trong mng tr v*/
            //tng t date  c thm vo comment mi ri
                //comment.id = state.comments.length;
                //comment.date = new Date().toISOString();
            return { ...state, comments: state.comments.concat(comment)}; //take care of adding new comment to Redux store

        case ActionTypes.COMMENTS_FAILED:
            return {...state, errMess: action.payload,commnents:[]};
    
        default:
            return state;
    }
};

