
//hm cu hnh store, nu c gi s tr v mt Redux store
//applyMiddleware  p dng 2 hm gia l logger v thunk
import { createStore, combineReducers,applyMiddleware } from 'redux';

//import cc reducers chuyn mn ha
import { Dishes } from './dishes';
import { Comments } from './comments';
import { Leaders } from './leaders';
import { Promotions } from './promotions';

//import 2 middlewares , now available and apply them to send data_arrays to reducers (Reducer will not store arrays anymore nh Lab08_2 trn github, state=DISHES)
import thunk from 'redux-thunk';
import logger from 'redux-logger';
import { createForms } from 'react-redux-form';
import { InitialFeedback } from './form'; //th hover chut ln InitialFeedback s thy configureStore ang import Model ca Form's dedicated state

//How to combine 4 small reducers ? s dng Redux.combineReducers
   ///inside combineReducer, say exactly how we combine 4 reducers
   ///to compose then overall global state, we have to  map these reducers into each 1 of 4 properties 

//Problem: ConfigureStore is declared but its value is never read  => Solution: export
export const ConfigureStore = () => {
    //you will need to create a store here
    const store =createStore(
        //this is how the combineReducers will take the Reducer mapping object, and then will map the smaller,simpler Reducer functions into various properties in the whole ground
        //hm Redux.combineReducers nhn i tng gi l ReducerMapObject
        //hm Redux.combineReducers turns an object whose values are different reducer functions, into a single reducer function. It will call every child functions and gather
        

        //hm dispatch(action) s gi 1 action object ti store nhm lm thay i state ang lu trong reducer
        combineReducers(
            {
                dishes:Dishes,  //CtrlClick v phi s ti ni cn thit
                comments:Comments,
                promotions:Promotions,
                leaders:Leaders,
                //feedback tng ng model InitialFeedback (nu khng th t tn cch khc nh form object) (gi l model v n l mt trong cc i tng nm trong state), do  feedback s nhn tt c thuc tnh ca InitialFeedback, v truy cp bng feedback.firstname
                //The createForms() function is meant to be used with combineReducers() from redux. Use it when you want a flatter combined reducer structure.
                ...createForms({ 
                    //to formReducer() qun l ring cc model con ca model InitialFeedback thuc global state, tng t Dishes reducer qun l state con v DISHES
                    //Muppla: createForms() adds form state to store => thut ng sai
                        /// createForms() ENABLES us to add form state to store, it create a reducer but takes care of the form only
                    feedback: InitialFeedback //Muppala: we supply feedback as InitialFeedback. Theo cch ny, ta  theo truyn thng added the necessary reducer for form and also state/model information into store
                    //Muppala: Another reason why we supply InitialFeedback is that later on we can RESET the form to its initial state after submitting the form
                    //Muppala:
                       /// Problem is Lab06_3_react_redux_form: After fill in the form, we F5 to reload the page th form khng c d liu no. Tuy nhin sau khi submit d liu th form khng c reset li nh c. WHY? 
                       /// Solution:  when you submit the form, you want the form reseted , for that I need to add a new action, see at MainComponent to add resetFeedbackForm
                })
                //Muppala: Dng createForms cho thy li ch ca react-redux-form supports al these on our behalf, do  we don't need to write Reducer and Action Creator , .... react-redux-form fills in all details by itself
            }
        ),
        //Tham s th hai l 1 Store enhancer (m rng/tng cng) c to bi applyMiddeware
        //trc  tt c connected Components  qua connect() u c Provider gip tt c c th truy cp Store available for all components
        //most common use case for middleware is to support asynchronous actions
            ///c th, middleware adds extra functionality to the Redux dispatch function
            /// trong khi , Store enhancers add extra functionality to the Redux store. 
        //https://redux.js.org/api/applymiddleware/  . https://askinglot.com/what-is-enhancer-redux
        // applyMiddleware( middleware chain) , Middleware is the suggested way to extend Redux with custom functionality
        applyMiddleware(thunk, logger) //chc nng tng t nh Provider
        //applyMiddleware is saying "thunk, logger" so that they are supplied into store as Store enhancer. Do Store  available cho ng dng, nn think v logger bn trong cng available cho ng dng

    );
    return store;
}

//The entire state of an application lists at a place called Store
//It acts as a brain and manages the status of the application. It also has a dispatch(action) function.


