import React, { Component } from 'react';
import './App.css';
import Main from './components/MainComponent'; //Hin ch dng redux cho mn n, khng cho dng connectedMain, th dish={this.props.dishes.filter((dish) => dish.featured)[0]} s b bo li undefined filter v khng nhn c mng dishes
import { HashRouter , BrowserRouter } from 'react-router-dom'; //HashRouter khng h tr history nn v hiu ha TransitionGroup v CSSTransition
import { Provider } from 'react-redux';
import { ConfigureStore } from './redux/configureStore';


const store = ConfigureStore();  //Ctrl+click vo ConfigureStore s ti ch file n tr ti
console.log(store);//ok, store is available to me

class App extends Component {
  
    
    render(){
      //the createStore() method which creates a store to keep together the state tree of your application.
      
      return (
        //the Provider component passes the store as props
        // thy N: the Provider component allows me to configure the React app so that the Redux tool become available to all components in React application
          /// Provider takes 1 attribute : store
          /// Ctrl+Click vo bin store v phi s tr ti const store bn trn u trang (cho thy s lin kt gia cc tn bin ging nhau), tng t CtrlClick ConfigureStore() u trang 
          /// khng c <ReactRouterDOM.BrowserRouter> s khng hin trang web
          <Provider store={store}> 
            <BrowserRouter>
               <div>
                  <Main/>
               </div>
            </BrowserRouter>
          </Provider>
        //  c BrowserRouter do  trong Main khng cn ti na
        //tip theo, connect()  kt ni React application to Redux store , ti trang MainComponent
        //react_devtools_backend.js:3973 Warning: <connectedMainComponent /> is using incorrect casing. Use PascalCase for React components, or lowercase for HTML elements.
      )
    }
}
export default App;



